<template>
  <div>
    <main> <!-- Début du main -->
        <form class="admin">
          <h1>Ajouter un auteur</h1>
          <label>Nom: <input v-model="person.name" @input="propose(person.name)"></label><br>
          <label>Lien: <input v-model="person.slug" @input="propose(person.slug)"></label><br>
          <label>Image: <imageInput v-model="person.img" /></label><br>
          <label>Date: <input v-model="person.date"></label>
          <h3>Biographie</h3>
          <editor v-model="person.bio"
          :options="defaultOptions"
          height="700px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="bio"/>
          <h3>Notes</h3>
          <editor v-model="person.notes"
          :options="defaultOptions"
          height="300px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="notes"/>
          <p><br><b>La fiche aura par défaut le badge "tout juste créé"</b></p>
          <input type="button" value="Annuler" @click="$router.push('/')">
          <input type="button" value="Créer" @click="save()">
          <BarLoader v-show="loading" style="margin: 0 auto; border-radius:5px, display: none"></BarLoader>
        </form>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import { mapActions, mapGetters } from 'vuex'
import { Editor } from '@toast-ui/vue-editor'
import ImageInput from '@/components/imageInput.vue'


export default {
  name: 'home',
  components: {
    Editor,
    ImageInput
  },
  data () {
    return {
        person: {date:""},
        loading: false,
        defaultOptions: {
            language: 'fr_FR',
            useCommandShortcut: true,
            useDefaultHTMLSanitizer: true,
            usageStatistics: true,
            hideModeSwitch: false,
            toolbarItems: [
                'heading',
                'bold',
                'italic',
                'strike',
                'divider',
                'hr',
                'quote',
                'divider',
                'ul',
                'ol',
                'indent',
                'outdent',
                'divider',
                'table',
                'image',
                'link',
                'divider',
                'code',
                'codeblock'
            ]
        }
    }
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ])
  },
  methods: {
    ...mapActions('persons',[
      'createPerson',
      'setPersons'
    ]),
    ...mapActions('alert',[
      'newAlert'
    ]),
    getHtml() {
        return this.$refs.Editor.invoke('getMarkdown')
        // return this.$refs.Editor.invoke('getHtml') -> check github @toast-ui.vue-editor issues, if getHtml method works
    },
    propose(input) {
        this.person.slug = (input.replace(/\W|\s+|-+/g, "-")).toLowerCase()
    },
    save() {
      this.loading = true;
      let form = new FormData();
      form.append("slug", this.person.slug)
      form.append("name", this.person.name)
      form.append("img", this.person.img)
      form.append("date", this.person.date)
      form.append("bio", this.$refs.bio.invoke('getMarkdown'))
      form.append("notes", this.$refs.notes.invoke('getMarkdown'))
        this.createPerson(form)
            .then(r=>{
                this.newAlert({type:'success', content:'Successfully edited'})
                this.setPersons(r.data);
                this.$router.push('/'+this.person.slug)
            })
            .catch(e=>{
                this.loading = false
                this.newAlert({type:'error', content:e.response.data})
            })
    }
  }
}
</script>
